.utility {
  grid-column: 1/3;
}
.btn {
  height: 4vw;
  /* width: 15vw; */
  margin: 20px;
  background-color: #282c34;
  border: 2px rgb(45, 111, 235) solid;
  border-radius: 5px;
  transition: 0.3s ease-in-out all;
  font-size: 1.5vw;
  font-weight: 700;
  color: white;
  outline: none;
}
@media only screen and (max-width: 600px) {
  .utility {
    grid-column: 1/2;
  }
  .btn {
    width: 40vw;
    margin: 5px;
    height: 10vw;
    font-size: 4vw;
  }
}
.btn:hover {
  background-color: rgb(45, 111, 235);
  cursor: pointer;
}
.btn:active {
}
